 .modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-content {
  background-color: white;
  padding: 20px;
  border-radius: 5px;
  width: 90%;
  max-width: 600px;
  max-height: 80%;
  display: flex;
  flex-direction: column;
}

.modal-header {
  margin-bottom: 20px;
}

.modal-body {
  flex-grow: 1;
  overflow-y: auto;
}

.form-group {
  margin-bottom: 15px;
}

.tags-input-container {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
}

.tag-item {
  background-color: #007bff;
  color: white;
  padding: 5px 10px;
  border-radius: 3px;
  display: flex;
  align-items: center;
}

.tag-text {
  margin-right: 5px;
}

.tag-remove {
  cursor: pointer;
}

.modal-footer {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  margin-top: 20px;
}
